}

.skills-container {
    display:flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.skills-container div {
    font-size: 15px;
    margin: 5px;
    padding: 5px;
    border: 1px solid #cca085; 
    border-radius: 5px;
    font-family: 'Montserrat';
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 180px;
    background-color: white;
    padding: 0 80px;
}

.contact-links a {
    text-decoration: none;
    color: inherit;
    padding: 24px;
}

.about-container {
    display: flex;
    justify-content: center;
}
.about-description {
    max-width: 700px;
}
.about-description p {
    font-size: 19px;
    font-weight: 400;
    text-align: justify;
}



#about-me-section-header p {
    border-bottom: 1px solid #2e3340;
}

.experience-list {
    list-style: none;
}
.experience-list li {
    border: 2px solid grey;
    margin: 24px;
    height: 400px;
}
